import Graph from "../../components/chart/chart";
import { useCallback, useEffect, useRef } from 'react';

const APIKEY = 'AIzaSyAd01GNKNnl9puewl_xMj2tcbpGiPUjJb8';
function GoogleMap() {
  const mapElement = useRef(null);
  //   ,   . 
  //  script   window.initMap    .
  const loadScript = useCallback((url: string) => {
    const firstScript = window.document.getElementsByTagName('script')[0];
    const newScript = window.document.createElement('script');
    newScript.src = url;
    newScript.async = true;
    newScript.defer = true;
    firstScript?.parentNode?.insertBefore(newScript, firstScript);
  }, []);


  // script google map api    callback 
  const initMap = useCallback(() => {
    const { google } = window;
    if (!mapElement.current || !google) return;


    const location = { lat: 37.3218778, lng: 126.8308848 };
    const map = new google.maps.Map(mapElement.current, {
      zoom: 17,
      center: location,
    });
    new google.maps.Marker({
      position: location,
      map,
    });
  }, []);


  useEffect(() => {
    const script = window.document.getElementsByTagName('script')[0];
    const includeCheck = script.src.startsWith(
      'https://maps.googleapis.com/maps/api'
    );


    // script   
    if (includeCheck) return initMap();


    window.initMap = initMap;
    loadScript(
      `https://maps.googleapis.com/maps/api/js?key=${APIKEY}&callback=initMap&language=en`
    );
  }, [initMap, loadScript]);


  return <div ref={mapElement}  style={{ height: "30vh" , display:"flex", width:"100%"}}/>;
}


export default GoogleMap;